<template>
    <div class="relative">
        <div class="rooms-obj flex gap-x-3 py-4" v-for="(room, index) in r" :key="index">
            <!-- rooms -->
            <div class="flex flex-col gap-y-3">
                <small class="text">Habitación {{index+1}}</small>
                <div class="flex justify-center">
                    <span class="cursor-pointer pointer border self-center text-white bg-orange rounded-4xl px-2 py-1 mx-2"
                    @click="subRoom(index)">
                        <i class="fa fa-minus"></i>
                    </span>
                    <span class="cursor-pointer pointer border self-center text-white bg-orange rounded-4xl px-2 py-1 mx-2"
                    @click="addRoom">
                        <i class="fa fa-plus"></i>
                    </span>
                </div>
            </div>
            <!-- adults -->
            <number-imput :title="'Adultos'" :min="1" :valor="r[index].adults" :id="index" @updVal="updAdult"></number-imput>
            <!-- children -->
            <number-imput :title="'Niños'" :valor="r[index].children" :id="index" @updVal="updChildren"></number-imput>
            <!-- infants
            <number-imput :title="'Infantes'" @updVal="(val) => {this.infants = val}"></number-imput> -->
        </div>
        <span class="absolute top-0 right-0 cursor-pointer h-auto" @click="closePerson"><i class="fa fa-times"></i></span>
    </div>
</template>
<script>
import NumberImput from '@/components/NumberImput'

export default {
    name: 'NumPerson',
    props: {
        r: {}
    },
    components: {
        NumberImput
    },
    data () {
        return {
            value: 0,
        }
    },
    methods: {
        updAdult(obj){
            this.r[obj.id].adults = obj.val
        },
        updChildren(obj){
            this.r[obj.id].children = obj.val
        },
        addRoom() {
            this.$emit('addRoom', {action: 'add'})
        },
        subRoom(index) {
            if (this.r.length > 1)
                this.$emit('subRoom', {action: 'del', pos: index})
        },
        closePerson() {
            this.$emit('close', false)
        }
   }
}
</script>
<style scoped>
    .rooms-obj {
        border-top: 2px solid #efefef;
    }
    .rooms-obj:first-child {
        border-top: 0 !important;
    }
</style>